.logo {
  width: 25%;
}

.nav-theme {
  background-color: #000;
  font-size: 20px;
}

.animate-navbar {
  box-shadow: 2px 10px 10px #000;
  animation: moveDown 0.5s ease-in-out;
}

@keyframes moveDown {
  from {
    transform: translateY(-5rem);
  }
  to {
    transform: translateY(0rem);
  }
}
