#skills{
  background-color:	#00BFFF;
  max-width:1500px;
  margin:auto;
}

@media (min-width: 576px)
.card-deck {
  margin-left:120px;
}
.focus {
  transition: transform 0.3s;
}

.focus:hover {
  transform: scale(1.05);
}

.card-title {
  font-size: 2em;
  font-weight: 700;
}

.image-style {
  width: 3em;
}

.font-details {
  font-size: 3em;
  font-weight: 500;
  color: #231d5af8;
}

.font-details-b {
  font-size: 2.5em;
  font-weight: 500;
  color:#fff;
}
